.ivanova {
    padding-top: 65px;
    .container {
        @extend %container;
    }
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 10px;
        @include media (md) {
            grid-template-columns: 584px 1fr;
            grid-template-rows: 1fr 180px;
            grid-row-gap: 40px;
            grid-column-gap: 135px;
            padding-bottom: 50px;
            border-bottom: 0.5px solid #C7C7C7;
        }
    }
    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include media (md) {
            order: 1;
        }
    }
    &__photo {
        position: relative;
        @include media (sm) {
            margin-top: 43px;
        }
        @include media (md) {
            margin-top: 55px;
        }
        &::after {
            content: "";
            background-image: url(../images/ivanova/heart.svg);
            display: block;
            width: 31px;
            height: 27px;
            position: absolute;
            top: 0px;
            right: -85px;
        }
    }
    &__img {
        margin-bottom: 23px;
    }
    &__title {
        @include text (18px, 500, 22px, $secondaryColor);
        margin-bottom: 14px;
        text-align: left;
    }
    &__photograf {
        margin-bottom: 7px;
        @include text (14px, 400, 21px, $secondaryColor);
    }
    &__link {
        max-width: 279px;
        text-align: center;
        margin-bottom: 15px;
        @include media (sm) {
            margin-bottom: 30px;
        }
        @include media (md) {
            margin-bottom: 0px;
        }
    }
    &__content-two {
        @include media (md) {
            order: 1;
        }
    }
    &__text {
        @include text (16px, 400, 24px, $secondaryColor);
        margin-bottom: 22px;
    }
    &__text-start {
        margin-bottom: 40px;
    }
    &__link-read {
        margin-top: 22px;
        position: relative;
    }
    &__link-read::after {
        content: "";
        background-image: url(../images/photocards/arrow.svg);
        display: block;
        width: 11px;
        height: 6px;
        position: absolute;
        top: 7px;
        right: -15px;
    }
}